<template>
  <div class="lg:text-[20px] text-[.25rem] flex w-full h-screen flex-col bg-gray-200">
    <Head title="学生请假"></Head>
    <main class="flex-1 overflow-scroll flex flex-col">
      <div class="top askFont flex justify-between items-center h-4 p-1 bg-white">
        <span class="">请假记录</span>
        <span class="text-green-500">请假记录</span>
      </div>
      <Form v-slot="formRef" ref="formRef" :form-structure="formStructure" v-model="form">
        <myButton value="提交请假条" @click="submit(formRef)"></myButton>
      </Form>
    </main>
  </div>
</template>
<script setup lang='ts'>
import Head from '@/components/head.vue';
import { ref, watch } from 'vue';
import Form from '@/components/form.vue';
import myButton from '@/components/form/myButton.vue';
import { userInfo as UserInfo } from '@/store/userInfo'
import { closeToast, showLoadingToast } from 'vant';
import { askLeave as AskLeave } from '@/request/askLeave/index'
import { showSuccessToast } from 'vant/es';
const userInfo = UserInfo()
const form = ref({
  studentName: userInfo.studentName,
  askReason: '病假',
  disease: '',
  special: '',
  isLeave: '需要离校',
  isWeek: false,//是否按周期请假
  leave: '设置时间',
  startTime: '',
  endTime: '',
  leaveSchoolTimes: [],
  isAtSchool: '非住校',
  scNumber: '',
  messageNotice: false
})
watch(() => form.value.isWeek, () => {
  if (form.value.isWeek) {
    if (formStructure.value?.[2]?.[1]?.hidden != undefined && formStructure.value?.[2]?.[4]?.hidden != undefined) {
      formStructure.value[2][1].hidden = true
      formStructure.value[2][4].hidden = false
    }

  } else {
    if (formStructure.value?.[2]?.[1]?.hidden != undefined && formStructure.value?.[2]?.[4]?.hidden != undefined) {
      formStructure.value[2][4].hidden = true
      formStructure.value[2][1].hidden = false
    }
  }
})
interface formItem {
  [key: string]: any
}
// const formRef = ref()
const formStructure = ref<formItem[][]>([
  [
    {
      type: 'labelSelect',
      label: '请假学生',
      key: 'studentName',
      options: {
        option: {
          name: "闫果",
          value: '闫果'
        }
      }
    },
    {
      type: 'labelButton',
      label: '请假原因',
      key: 'askReason',
      value: ['病假', '事假']
    },
    {
      hidden: false,
      type: 'muliButton',
      key: 'disease',
      value: ['感冒', '发烧', '腹泻', '水痘', '咳嗽', '手足口', '咽喉炎', '其他疑似传染病', '其他非传染病']
    },
    {
      type: 'fieldInput',
      key: 'special',
      placeholder: '特殊情况说明(选填)',
    },
  ],
  [
    {
      type: 'labelButton',
      label: '离校需求',
      key: 'isLeave',
      value: ['需要离校', '无需离校']
    }
  ],
  [
    {
      type: 'labelSwitch',
      key: 'isWeek',
      label: '按周期请假'
    },
    {
      hidden: false,
      type: 'labelButton',
      label: '是否立即请假',
      value: ['设置时间', '立即请假'],
      key: 'leave'
    },
    {
      type: 'labelTime',
      label: '开始日期',
      key: 'startTime',
      valida: true,
      rules: [
        {
          required: true,
          message: '不能为空'
        }
      ]
    },
    {
      type: 'labelTime',
      label: '结束日期',
      key: 'endTime',
      valida: true,
      rules: [
        {
          required: true,
          message: '不能为空'
        }
      ]
    },
    {
      hidden: true,
      type: 'labelCheck',
      label: '请假频次',
      key: 'leaveSchoolTimes',
      value: ['星期一', '星期二', '星期三', '星期四', '星期五', '星期六', '星期日']
    },
  ],
  [
    {
      type: 'labelButton',
      label: '是否住校',
      value: ['住校', '非住校'],
      key: 'isAtSchool'
    },
    {
      hidden: true,
      type: 'labelInput',
      label: '宿舍编号',
      key: 'scNumber',
      placeholder: '请输入宿舍号或名称',
      valida: false,
      rules: [
        {
          pattern: /\d+/i,
          message: '请输入数字'
        }
      ]

    }
  ],
  [
    {
      type: 'labelSwitch',
      label: '提交请假条后,给审批人发送短信通知',
      key: 'messageNotice'
    },
  ]
])
const submit = (formRef: any) => {

  formRef.formRef.validate().then(() => {
    showLoadingToast({
      message: '加载中...',
      forbidClick: true,
      loadingType: 'spinner',
      overlay: true,
      className: ['p-4', 'flex', 'justify-center', 'items-center', 'flex-col'],
      iconSize: '50px'
    });
    AskLeave('/api/askLeave', 'post', {
      ...form.value
    }).then(res => {
      console.log(res)
      closeToast()
      showSuccessToast('提交成功')
    }).catch(() => {
      closeToast()
      showToast('提交失败')
    })
  }).catch((error: Error) => {
    console.log('验证失败', error)
  })
}
watch(() => form.value.isAtSchool, () => {
  if (form.value?.isAtSchool && formStructure.value?.[3]?.[1]?.hidden != undefined) {
    if (form.value.isAtSchool == '非住校') {
      formStructure.value[3][1].hidden = true
      formStructure.value[3][1].valida = false
    }
    else {
      formStructure.value[3][1].hidden = false
      formStructure.value[3][1].valida = true
    }
  }

})
// 监听请假原因变化
watch(() => form.value.askReason, (newValue) => {
  // 安全地访问和修改 hidden 属性
  if (formStructure.value[0] && formStructure.value[0][2]) {
    if (newValue === '病假') {
      formStructure.value[0][2].hidden = false
      formStructure.value[0][2].valida = true
    } else if (newValue === '事假') {
      formStructure.value[0][2].hidden = true
      formStructure.value[0][2].valida = false
    }
  }
}, { immediate: true }) // 立即执行一次以设置初始状态

</script>
<style scoped lang='less'></style>
