<template>
  <div>
    <van-field @click="show = true" class="askFont m-0! p-0!" :label="props.label" placeholder="" readonly
      v-model="modelValueString" right-icon="arrow"></van-field>
    <van-popup v-model:show="show" position="bottom" :style="{ height: '70%' }" class="py-3">
      <van-checkbox-group v-model="modelValue">
        <van-cell-group inset>
          <van-cell @click="toggle(index)" v-for="(item, index) in props.value" :key="index">
            <template #value>
              <div class="flex ">
                <van-checkbox class="askFont" :name="item" :ref="(el: HTMLElement) => checkboxRefs[index] = el"
                  @click.stop>{{ item
                  }}
                </van-checkbox>
              </div>
            </template>
          </van-cell>
        </van-cell-group>
      </van-checkbox-group>
      <div class="ml-3">
        <van-button @click="cacel" class="mr-1! askFont" text="取消" type="warning"></van-button>
        <van-button @click="ok" text="确定" class="askFont" type="primary"></van-button>
      </div>

    </van-popup>
  </div>
</template>
<script setup lang='ts'>
import { computed, ref, } from 'vue';

const props = defineProps(['label', 'value'])
const modelValue = defineModel<string[]>({ type: Array, default: () => [] })
const modelValueString = computed(() => {
  return modelValue.value?.join(',')
})
const show = ref<boolean>(false)
const checkboxRefs = ref<any[]>([])
const toggle = (index: number) => {
  checkboxRefs.value[index].toggle()
}
const cacel = () => {
  modelValue.value = []
  show.value = false
}
const ok = () => {
  show.value = false
}
</script>
