<template>
  <div>
    <van-field class="p-0! pb-1! m-0! askFont" :rules="props.rules" v-model="modelValue" @click="showBottom = true" :label="props.label"
      right-icon="arrow" input-align="right" readonly  />
    <!-- 底部弹出 -->
    <van-popup v-model:show="showBottom" position="bottom" :style="{ height: '70%' }">
      <van-picker-group class="askFont" title="预约日期" :tabs="['选择日期', '选择时间']" @confirm="onConfirm" @cancel="onCancel">
        <van-date-picker class="askFont" v-model="currentDate" :min-date="minDate" :max-date="maxDate" />
        <van-time-picker class="askFont" v-model="currentTime" />
      </van-picker-group>
    </van-popup>
  </div>
</template>
<script setup lang='ts'>
import { computed, ref, watch } from 'vue';
const showBottom = ref(false)
const props = defineProps(['label', 'value', 'rules'])
const modelValue = defineModel()
const currentDate = ref([])
const currentTime = ref([])
const dataTime = computed(() => {
  return currentDate.value.join('-') + ' ' + currentTime.value.join(':')
})
watch(dataTime, () => {
  modelValue.value = dataTime.value
})
const minDate = ref()
const maxDate = ref()
const onConfirm = () => {
  showBottom.value = false
}
const onCancel = () => {
  modelValue.value = ''
  showBottom.value = false
}
</script>
<style scoped lang='less'></style>
